import React from "react";

const RegistryHeader = props => (
  <div className="container text-center">
    <div className="row">
      <div className="col-lg-12">
        <div className="card">
          <div className="card-header text-center text-white bg-dark grey">
            <h3 className="card-title  mt-2">
              <strong>
                <i className="fa fa-newspaper-o" aria-hidden="true" /> This Is
                What I Want
              </strong>
            </h3>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default RegistryHeader;



